// 2
export class Vector {
    private x: number;
    private y: number;

    // 
    constructor() {
        this.x = 0;
        this.y = 0;
    }
    // x
    public getX(): number {
        return this.x;
    }
    // x
    public setX(aX: number): void {
        this.x = aX;
    }
    // y
    public getY(): number {
        return this.y;
    }
    // y
    public setY(aY: number): void {
        this.y = aY;
    }
    // x,y
    public setXY(aX: number, aY: number): void {
        this.x = aX;
        this.y = aY;
    }
    // x,yVector
    public set(vec: Vector): void {
        this.x = vec.x;
        this.y = vec.y;
    }
    // 
    public add(vec: Vector): void {
        this.x += vec.x;
        this.y += vec.y;
    }
    // 
    public sub(vec: Vector): void {
        this.x -= vec.x;
        this.y -= vec.y;
    }
    // 
    public multiply(m: number): void {
        this.x *= m;
        this.y *= m;
    }
    // 
    public divide(d: number): void {
        this.x /= d;
        this.y /= d;
    }
    // 
    public equals(vec: Vector): boolean {
        return this.x === vec.x && this.y === vec.y;
    }
    // 
    public distance(vec: Vector): number {
        let dx = this.x - vec.x;
        let dy = this.y - vec.y;
        return Math.sqrt(dx * dx + dy * dy);
    }
    // 
    public norm(): number {
        return Math.sqrt(this.x * this.x + this.y * this.y);
    }
}