// 
export class Layer {
    private layerId: number;        // ID
    private texture: WebGLTexture;  // 

    // 
    constructor(id: number, tex: WebGLTexture) {
        this.layerId = id;
        this.texture = tex;
    }

    // ID
    public getLayerId(): number {
        return this.layerId;
    }

    // 
    public setTexture(tex: WebGLTexture): void {
        this.texture = tex;
    }

    // 
    public getTexture(): WebGLTexture {
        return this.texture;
    }
}